import { types } from "../types";

// un Reducer recibe un estado y una accion
export const authReducer = (state = {}, action) => {

    switch (action.type) {
        case types.login:
            return {
                uid: action.payload.uid,
                name: action.payload.displayName
            };

        case types.logout:
            return {};

        default:
            return state;
    }

}
